import {
  LoginBox,
  Idbox,
  PasswordBox,
  LoginBtn,
  HelpBox,
  Remember,
  LoginBoxBtm,
  LoginBoxTop,
  LoginWrapper,
  LoginH2,
  LoginInput,
  BtnBox,
  Captcha,
} from "../Components/styled-components/LoginStyled";

function Login() {
  return (
    <>
      <LoginWrapper>
        <LoginBox>
          <LoginBoxTop>
            <LoginH2></LoginH2>
            <Idbox>
              <LoginInput
                type="text"
                placeholder="E-mail    "
              ></LoginInput>
            </Idbox>

            <PasswordBox>
              <LoginInput type="password" placeholder=""></LoginInput>
            </PasswordBox>
            <BtnBox>
              <LoginBtn> </LoginBtn>
            </BtnBox>

            <Remember>
              <input type="Checkbox"></input>
              <label> </label>
            </Remember>
          </LoginBoxTop>
          <LoginBoxBtm>
            <HelpBox>
               ? <a href="#"> </a>
            </HelpBox>
            <Captcha>
                 reCAPTCHA   
              .
              <a href="#"> </a>
            </Captcha>
          </LoginBoxBtm>
        </LoginBox>
      </LoginWrapper>
    </>
  );
}

export default Login;
