const API_KEY = "f7a78a42da1220e778079ece954d5f72";
const BASE_PATH = "https://api.themoviedb.org/3";

interface IResults {
  release_date: string;
  backdrop_path: string;
  poster_path: string;
  title: string;
  overview: string;
  id: number;
  name: string;
}

export interface IGetResult {
  dates: { maximum: string; minimum: string };
  page: number;
  results: IResults[];
  total_pages: number;
  total_results: number;
  release_date: string;
  vote_average: number;
}

export interface IGetDetails {
  id: number;
  backdrop_path: string;
  poster_path: string;
  overview: string;
  vote_average: number;
  genres: [
    {
      id: number;
      name: string;
    }
  ];
  // ------  
  title: string;
  original_title?: string;
  // ------ TV 
  name: string;
  original_name?: string;
  first_air_date: string;
  release_date: string;
}

export interface IGetCredits {
  id: number;
  cast: [
    {
      id: number;
      name: string;
      original_name: string;
      character: string;
    }
  ];
  crew: [
    {
      id: number;
      known_for_department: string;
      name: string;
    }
  ];
}

export interface ISearchResult {
  id: number;
  name?: string;
  title?: string;
  backdrop_path: string;
  poster_path: string;
  vote_average: number;
  overview: string;
  original_title: string;
  release_date?: string;
  first_air_date?: string;
}

export interface IGetSearch {
  page: number;
  results: ISearchResult[];
  total_pages: number;
  total_results: number;
  dates: string;
}

// api 
export function getMovies(category: string) {
  return fetch(
    `${BASE_PATH}/movie/${category}?api_key=${API_KEY}&language=ko-KR`
  ).then((response) => response.json());
}
export function getMovieDetail(id: string) {
  return fetch(
    `${BASE_PATH}/movie/${id}?api_key=${API_KEY}&language=ko-KR`
  ).then((response) => {
    return response.json();
  });
}
export function getMovieCredit(id: string) {
  return fetch(
    `${BASE_PATH}/movie/${id}/credits?api_key=${API_KEY}&language=ko-KR`
  ).then((response) => {
    return response.json();
  });
}

//TV api 
export function getSeries(tvCategory: string) {
  return fetch(
    `${BASE_PATH}/tv/${tvCategory}?api_key=${API_KEY}&language=ko-KR&page=1`
  ).then((response) => {
    return response.json();
  });
}

export function getSeriesDetail(tv_id: string) {
  return fetch(
    `${BASE_PATH}/tv/${tv_id}?api_key=${API_KEY}&language=ko-KR`
  ).then((response) => {
    return response.json();
  });
}

export function getSeriesCredit(tv_id: string) {
  return fetch(
    `${BASE_PATH}/tv/${tv_id}/credits?api_key=${API_KEY}&language=ko-KR`
  ).then((response) => {
    return response.json();
  });
}

// ,  
export function getSearchMovie(keyword: string) {
  return fetch(
    `${BASE_PATH}/search/movie?api_key=${API_KEY}&language=ko-KR&query=${keyword}&page=1`
  ).then((response) => {
    return response.json();
  });
}

export function getSearchTv(keyword: string) {
  return fetch(
    `${BASE_PATH}/search/tv?api_key=${API_KEY}&language=ko-KR&query=${keyword}&page=1`
  ).then((response) => {
    return response.json();
  });
}
